@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
*{
	margin:0px;
	padding:0px;
}

body{
	background-image: linear-gradient(to top, #a233de, #020d63);
	background-repeat:no-repeat;
	height: 100%;
}

.search-container{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction:column;
	min-height: 100vh;
}


/*<div className="search">
	<h1 className="search-title"> Search Movie </h1>
	<input className="search-input" type="text" placeholder="Type its name"/> 
</div>*/

.search{
	display:flex;
	flex-direction:column;
	font-family: 'Quicksand', sans-serif;
	align-items: center;
	margin-top:100px;
}

.search .search-title-unvisible{
	opacity: 0;
	font-size:50px;
}

.search .search-title{
	margin:20px;
	font-size:50px;
	color:#94ecf7;
	opacity: 1;
	transition: opacity 2s ease-in;
}

.search .search-input-container{
	display: flex;
	align-items: center;
}

.search .search-input-unvisible{
	padding:7px;
	font-size:15px;
	width:80%;
	opacity: 0;
	transform:translate(0px,100px);
}

.search .search-input{
	padding:7px;
	background: #535d5e;
	border-radius:10px;
	color:lightgrey;
	font-size:15px;
	width:200px;

	transition:1.5s;
	opacity: 1;
}

.search .search-icon-unvisible{
	margin-left:10px;
	opacity: 0;
}

.search .search-icon{
	margin-left:10px;
	color:lightgrey;
	opacity: 1;
	transition:2s;
}

.search .search-input::-webkit-input-placeholder{
	color:lightgrey;
	text-align: center;
	-webkit-transition:2s;
	transition:2s;
}

.search .search-input:-ms-input-placeholder{
	color:lightgrey;
	text-align: center;
	-ms-transition:2s;
	transition:2s;
}

.search .search-input::-ms-input-placeholder{
	color:lightgrey;
	text-align: center;
	-ms-transition:2s;
	transition:2s;
}

.search .search-input::placeholder{
	color:lightgrey;
	text-align: center;
	transition:2s;
}

.search .search-input:focus{
	outline: none;	
}

.search .search-input:focus::-webkit-input-placeholder{
	-webkit-transition: 1s;
	transition: 1s;
	transform:translate(100px,0px);
	color:transparent;
}

.search .search-input:focus:-ms-input-placeholder{
	-ms-transition: 1s;
	transition: 1s;
	transform:translate(100px,0px);
	color:transparent;
}

.search .search-input:focus::-ms-input-placeholder{
	-ms-transition: 1s;
	transition: 1s;
	transform:translate(100px,0px);
	color:transparent;
}

.search .search-input:focus::placeholder{
	transition: 1s;
	transform:translate(100px,0px);
	color:transparent;
}

.search .search-input:focus ~ i {
	transition:0.7s;
	transform:translate(10px,0px);
	font-size:20px;
	color:#94ecf7;
}


/*End of search input and title*/

/*
<div className="search-results">
	<h2 className="search-results-header"> Results Header </h2>
</div>
*/

.search-results-unvisible{
	width:0px;
	opacity: 0;
	margin-top:30px;
	font-size:13px;
}

.search-results{
	padding:15px;
	background:#36626b;
	margin-top:30px;
	color:lightgrey;
	border-radius: 15px;
	border:medium outset grey;
	font-family: sans-serif;
	font-size:13px;
	width:80vw;
	opacity:1;
	transition:2s;
	text-align: center;
}

.search-results-body{
	display:flex;
	flex-wrap: wrap;
	justify-content: center;
}

.movie{
	margin:10px;
}

.movie{
	width:200px;
	height:270px;
	background-size: cover;
}

.movie .movie-body{
	padding:30px;
	height:210px;
	opacity: 0;
	transform: scale(0.4);
    transform-origin: bottom right;
}

.movie:hover .movie-body{
	background: #590000;
	opacity: 1;
	transition: 0.6s;
	transform: scale(1);
}

.movie .movie-text{
	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;
	justify-content: space-between;
	transform: translateY(50px);
}

.movie .movie-text .movie-year{
	margin-bottom: 20px
}




.navbar{
	display:flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	top:0px;
	right:0px;
	left:0px;
	background:#0a0f4f;
	color:#94ecf7;
	font-family: 'Quicksand', sans-serif;
	padding:0px 30px;
	z-index: 500;
}

.navbar .navbar-title{
	font-size:25px;
	color:#baffeb;
}

.navbar .navbar-ul{
	display:flex;
	margin:0px;
}

.navbar .navbar-li:hover{
	background: #8acfbb;
	color:#0a0f4f;
}

.navbar .navbar-li{
	padding:25px;
	list-style-type:none;
}
